@import "ScssHelpers/helpers-all.scss";

.your_subscription {
	&:not(:last-child) {
		margin-bottom: 4.4rem;
	}
}

.your_subscription__title {
	@include h3;
	text-align: center;

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 3.5rem;
		}

		@include bp($point_4) {
			margin-bottom: 1.9rem;
		}
	}

	@include bp($point_4, min) {
		text-align: center;
	}

	@include bp($point_4) {
		text-align: left;
	}
}

.your_subscription__container {
	background-color: $gray_3;

	@include bp($point_2, min) {
		padding: 4.4rem 10.2rem 7rem 8rem;
	}

	@include bp($point_3, $point_4) {
		padding: 4.4rem 3.2rem 7rem 3.2rem;
	}

	@include bp($point_4 - 1) {
		padding: 2.1rem 2.4rem 4rem;
	}
}

.your_subscription__list {
	margin: 0;
	padding: 0;

	list-style: none;
}

.your_subscription__item {
	&:not(:last-child) {
		margin-bottom: 5rem;
	}
}

.your_subscription__head {
	@include flex_block(row, wrap, space-between, center);
	cursor: pointer;

	//&:not(:last-child) {
	//	margin-bottom: 1.4rem;
	//}

	@include bp($point_4, min) {
		padding: 0 0 1.4rem;
		border-bottom: 2px solid $white;
	}

	@include bp($point_4 - 1) {
		padding: 0 0 .8rem;
		border-bottom: 1px solid $white;
	}
}

.your_subscription__head_title {
	@include h5;
	text-transform: uppercase;
	transition: opacity .3s ease;

	.your_subscription__head:hover & {
		opacity: .6;
	}
}

.your_subscription__head_state {
	@include flex_block(row, nowrap, flex-start, center);
	color: $gold_2;

	transition: opacity .3s ease;

	.your_subscription__head:hover & {
		opacity: .6;
	}
}

.your_subscription__head_state_spoiler {
	&:after {
		content: attr(data-open);
	}

	.your_subscription__item.closed_state & {
		&:after {
			content: attr(data-close);
		}
	}

	@include bp($point_4, min) {
		@include spoiler_acc(1.4rem);
	}

	@include bp($point_4 - 1) {
		margin-right: .2rem;
		text-transform: uppercase;

		@include spoiler_acc(1.4rem);
	}
}

.your_subscription__head_state_icon {
	@include spoiler_acc_icon;
	margin-left: .8rem;

	transform: rotate(-180deg);

	.your_subscription__item.closed_state & {
		transform: rotate(0);
	}
}

/*===============Body===================*/

.your_subscription__body {
	width: 100%;
	//max-height: 0;
	//opacity: 0;
	//
	//transition: all 0.25s ease-out;
	//will-change: max-height;
	//overflow: hidden;
	//pointer-events: none;
	//
	//.your_subscription__item--active_state & {
	//	opacity: 1;
	//	pointer-events: auto;
	//}
}

.your_subscription__body_in {
	//padding: 2.2rem 0 3.2rem;
	width: 100%;
}

.your_subscription__row {
	margin-top: 2.2rem;

	.your_subscription__item.closed_state & {
		&:not(.checked_state) {
			display: none;
		}

		.quantity {
			.input_number_increment,
			.input_number_decrement {
				@include bp($point_3) {
					display: none;
				}
			}

			.input_number {
				color: $gold_2;
				pointer-events: none;
			}
		}
	}

	@include bp($point_4, min) {
		@include flex_block(row, nowrap, flex-start, center);
	}

	@include bp($point_4 - 1) {
		@include flex_block(row, wrap, flex-start, center);
	}
}

.your_subscription__col_check {
	@include bp($point_4, min) {
		width: 5%;
	}

	@include bp($point_4 - 1) {
		display: none;
	}

	.form_checkbox__label_size_1 {
		font-size: 1.6rem;
	}
}

.your_subscription__col_quantity {

	@include bp($point_4, min) {
		width: 21%;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		margin-top: 0.8rem;
		order: 4;
	}
}

.your_subscription__col_name {
	@include bp($point_4, min) {
		width: 36%;
		padding: 0 2.4rem;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		margin-bottom: .2rem;
	}
}

.your_subscription__col_number {
	@include bp($point_4, min) {
		width: 25%;
		padding: 0 2rem;
		text-align: center;
	}

	@include bp($point_4 - 1) {
		width: 50%;
	}
}

.your_subscription__col_prise {


	@include bp($point_4, min) {
		width: 18%;
		padding: 0 2.6rem;
	}

	@include bp($point_4 - 1) {
		width: 50%;
		text-align: right;
	}
}

.your_subscription__quantity {
	margin: 0 auto;
	flex-shrink: 0;

	@include bp($point_4, min) {
		width: 12.4rem;
	}

	@include bp($point_4 - 1) {
		width: 100%;
	}
}

.your_subscription__name,
.your_subscription__number,
.your_subscription__price {
	//padding: 0 1rem;

	@include bp($point_4, min) {
		font-size: 1.8rem;
	}
}

.your_subscription__name {
	font-weight: 500;

	@include bp($point_4 - 1) {
		letter-spacing: -.01em;
		font-size: 1.8rem;
	}
}

.your_subscription__number,
.your_subscription__price {
	@include bp($point_4 - 1) {
		font-size: 1.6rem;
		letter-spacing: -.06em;
	}
}
